@import "./core/reset";
@import "./core/variables";
@import "./core/mixins";
@import "./html.scss";


.hero {
  display: grid;
  top: 0;
 
}

.header {
  position: fixed;
  height: 40px;
  width: 100%;
  background-color: transparentize(#5DBACC, 0.3);
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;


  &__title {
    font-size: 30px;
    display: inline;
    font-family: 'Dancing Script', cursive;
  }
}

.links {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content:space-evenly;
  flex-wrap: wrap;
  background-color: black;
  top: 40px;
  width: 100%;
  padding: 5px;
  z-index: 9999;

  &__home, &__about, &__techstack, &__workexperience, &__contact, &__projects {
    display: inline-block;
    position: relative;
    color: white;
    // color: transparentize(#fff066, 0.3);
  }

  &__home::after, &__about::after, &__techstack::after, &__workexperience::after, &__contact::after, &__projects::after {
    content: '';
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: 3px;
    bottom: 0;
    left: 0;
    background-color: #5DBACC;
    transform-origin: bottom left;
    transition: transform 0.25s ease-out;
    border-radius: 2px;
  }

  &__home:hover::after, &__about:hover::after, &__techstack:hover::after, &__workexperience:hover::after, &__contact:hover::after, &__projects:hover::after {
    transform: scaleX(1);
    transform-origin: bottom left;
  }
}



.introduction {
  position: relative;
  display: block;
  width: 40%;
  top: 150px;
  text-align: left;
  left: 100px;

  &__title {
    position: relative;
    font-size: 30px;
    font-family: 'Inclusive Sans', sans-serif;
    padding: 15px;
  }

  &__text {
    display: flex;
    padding: 15px;
    
    &--me{
        font-size: 14px;
        padding-right: 5px;
    }

    &--name{
        font-size: 30px;
        line-height: 1;
        background-color: $colorPrincipal;
        color: white;
        width: 100px;
        height: 30px;
        text-align: center;
    }
    
  }

  &__profession {
    padding: 10px;
    font-size: 25px;
  }
  
}



.aboutme {
  position: relative;
  padding-left: 75px;
  top: 230px;
  width: 65vw;
  text-align: left;


  &__title {
    font-size: 30px;
  }

  &__text {
    padding-top: 20px;
    line-height: 23px;
    font-size: 17px;

    &--hl{
      background-color: transparentize($colorSecundary, 0.70s);
    }

    &--1 {
      text-indent: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    &--2 {
      text-indent: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  }

}



