.workexperience {
  text-align: left;
  position: relative;
  padding-left: 75px;
  top: 60px;
  width: 65vw;
  text-align: left;
  padding-bottom: 180px;


  &__title {
    font-size: 30px;
    padding-bottom: 20px;;
  }

  &__subtitle {
    background-color: $colorPrincipal;
    padding: 10px;
    color: white;
  }

  &__text {
    padding-top: 20px;
    line-height: 23px;
    font-size: 17px;

    &--hl{
      background-color: transparentize($colorSecundary, 0.70s);
    }

    &--1 {
      text-indent: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
    }

    &--2 {
      text-indent: 10px;
      margin-top: 20px;
      margin-bottom: 20px;
     

      &-duties {
         padding-top: 15px;
         li {
          list-style-type: circle;
        
         }
         li::marker {
          content: '‚úù ';
          font-size: 1.2em;
          color: $colorSecundary;
         }
      }
    }
