.projects__title{
  font-size: 30px;
  text-align: left;
  padding-left: 75px;
}

.proyectos {
  margin-top: 30px;
  margin-bottom: 100px;
  padding-left: 75px;
  padding-right: 75px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  align-content: center;
  flex-wrap: wrap;

  &__rectangle {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: $colorSecundary;
    height: 300px;
    border-radius: 10px;
    width: 100%;
    margin-bottom: 30px;
    flex-wrap: wrap;
    padding: 10px;
    margin: 10px;
  }

  // &__title {
  //   margin-top: 70px;
  //   padding-left: 37px;
  //   padding-right: 36px;
  //   font-size: 30px;
  //   background-color: #070707;
  // }
  &__name {
    font-size: 18px;
  }
  &__description {
    text-align: center;
    font-size: 12px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  &__list {
    display: flex;
    &--link {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 40px;
      height: 40px;
      background-color: white;
      margin-right: 10px;
      border-radius: 5px;
      &:hover {
        background-color: rgb(79, 76, 76);
        color: white;
      }
    }
  }
  &__enlace {
    &--link {
      &:hover {
        color: white;
      }
    }
  }
  &__image {
    width: 200px;
    height: 150px;
    padding-top: 10px;
  }
  &__button {
    background-color: $colorPrincipal;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    height: 30px;
    width: 100%;
    border-radius: 5px;
    color: white;
    font-weight: bold;
    margin-bottom: 20px;
  }
  @include tablet {
    display: flex;
    // justify-content: space-between;
    &__rectangle {
      width: 210px;
      justify-content: space-evenly;
    }
    &__name {
      font-size: 18px;
    }
    &__description {
      font-size: 14px;
    }
    &__list {
      &--link {
        width: 40px;
        height: 40px;
      }
    }
    &__image {
      width: 150px;
      height: 100px;
      padding-top: 0;
    }
    &__button {
      font-size: 16px;
      height: 35px;
      width: 100%;
    }
  }
  @include desktop {
    &__rectangle {
      height: 310px;
      width: 100%;
    }
    &__name {
      font-size: 18px;
    }
    &__description {
      font-size: 16px;
      width: 250px;
    }
    &__list {
      &--link {
        width: 40px;
        height: 40px;
      }
    }
    &__image {
      width: 200px;
      height: 150px;
      padding-top: 25px;
      margin-bottom: 20px;
    }
    &__button {
      font-size: 16px;
      height: 35px;
      width: 300px;
    }
  }
}

.section-proyects {
  @include desktop {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
  }
}

.projects-list {
  @include tablet {
    display: grid; 
    grid-template-columns: 210px 210px 210px; 
    grid-template-rows: 1fr 1fr 1fr; 
    gap: 20px 20px;
  }
  @include desktop {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(16rem, 1fr));
    gap: 40px;
    margin-left: 150px;
    margin-right: 150px;
  }
}




.footer{
  position: fixed;
  text-align: center;
  background-color: #070707;
  height: 5%;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  
  &__legal {
    color: white;
  }
}




